export function ReceivesVisitForm () {
  return App.CreateElement ('div', null, 
    App.CreateElement ('br', null),
    App.CreateElement (Main.TextField, { type: 'text', label: 'Parente', name: 'whovisits' }),

    App.CreateElement (Main.TextField, { type: 'date', label: 'Data da Visita', name: 'visitdate' }),

    App.CreateElement (Main.ListSelect, { label: 'Período da Visita' },
      App.CreateElement ('option', { value: 1 }, 'Manhã'),
      App.CreateElement ('option', { value: 2 }, 'Tarde'),
      App.CreateElement ('option', { value: 3 }, 'Noite')
    ),

    App.CreateElement (Main.TextField, { type: 'time', label: 'Horário da Visita', name: 'visittime' }),
  );
}

