export function Form ({ children, ...props }) {

  function handleFormSubmit ({ preventDefault }) {
    preventDefault ();
  }

  return App.CreateElement ('form', Main.MergeObjects (props, {
    onSubmit: handleFormSubmit
  }), children);
}

export function SaveButton (props) {

  function getElementParentForm (element) {
    if (element instanceof HTMLFormElement) {
      return element;
    }

    const parentElement = element.parentNode;

    if (parentElement instanceof HTMLFormElement) {
      return parentElement;
    } else {
      return getElementParentForm (parentElement);
    }
  }

  function getFormInputFields (form) {
    const formProps = {};

    const formFieldTypeBinds = {
      'bindcheckboxfield': function bindcheckboxfield (field) {
        formProps [field.name] = formProps [field.name] instanceof Array ? formProps [field.name] : [formProps [field.name]];

        if (field.checked) {
          formProps [field.name].push (field.value);
        }
      },

      'bindradiofield': function bindradiofield (field) {
        if (field.checked) {
          formProps [field.name] = field.value;
        }
      },

      'default': function (field) {
        formProps [field.name] = field.value;
      }
    };

    Array.from (form.querySelectorAll ('input'))
      .map (formChildNodesMap);

    function formChildNodesMap (field) {
      /* formProps [ field.name ] = field.value; */
      const formFieldTypeBind = ['bind', field.type, 'field'].join ('');

      if (typeof formFieldTypeBinds [formFieldTypeBind] == 'function') {
        formFieldTypeBinds [formFieldTypeBind].apply (this, [field]);
      } else {
        formFieldTypeBinds ['default'].apply (this, [field]);
      }
    }

    return formProps;
  }

  function handleButtonClick (e) {
    e.preventDefault ();

    const parentForm = getElementParentForm (e.target);

    if (parentForm instanceof HTMLFormElement) {
      const formInputFields = getFormInputFields (parentForm);
      
      console.log (formInputFields);
    }

    const tab = Tab.find ('formTab');

    tab.next ();

    if (typeof props.onClick === 'function') {
      return props.onClick.apply (this, arguments);
    }
  }

  return App.CreateElement ('div', {'class': 'form-submit'}, 
    App.CreateElement ('button', {onClick: handleButtonClick, 'class': 'form-textfield', type: 'submit'}, 'Salvar e Continuar')
  );    
}
