import cep from 'cep-promise';

import Main from '~/Main';

import { App } from '~/App';
import { keyCodes } from '~/key-codes';
import { Form, SaveButton } from '@components/index';

export function AddressTab () {
  const cepOutputRef = {};
  const state = App.UseState ({ 
    componentDidUpdate: function () {
      console.log (arguments)
    },
    cep: '' 
  });

  function fillCEPPropFields () {
    const cepCode = state.cep || null;

    cepOutputRef.current.innerHTML = '';
    
    cep (cepCode, { timeout: 5000, providers: ['brasilapi'] })
      .then (function (cepData) {
        Object.keys (cepData).map (function (key) {
          cepOutputRef.current.appendChild (
            App.CreateElement (Main.TextField, {
              label: key.toUpperCase (),
              name: key,
              value: cepData [key]
            })
          );
        });
      })
      .catch (() => {
        cepOutputRef.current.innerHTML = 'CEP Inválido!!';
      })
  }

  function handleInputBlur () {
    fillCEPPropFields ();
  }

  function handleInputKeyDown (e) {    
    if ([keyCodes.ENTER, keyCodes.TAB].indexOf (e.keyCode) >= 0) {
      e.preventDefault ();
      fillCEPPropFields ();
    }
  }
  
  return App.CreateElement (Form, {method: 'post', 'action': '/register', onSubmit: () => null}, 
    App.CreateElement ('div', null,
      App.CreateElement (Main.TextField, { 
        type: 'text', 
        value: state.cep, 
        label: 'CEP',
        onChange: e => state.setState ({ cep: e.target.value }),
        onKeyDown: handleInputKeyDown,
        onBlur: handleInputBlur
      }),
      App.CreateElement ('div', {ref: cepOutputRef}),
      App.CreateElement (SaveButton, {})
    )
  );
};
