import { DashboardPageMainAction } from "./dashborad";
import { GetElementHeight } from "./Main";

export const ActionPerformer = [
  DashboardPageMainAction,

  function () {
    const formContainer = uolkeo.$$qs ('.form-container');
    const mainLogo = uolkeo.$$qs ('.main-logo');
    
    if (formContainer instanceof HTMLElement) {
      var formContainerHeight = GetElementHeight (formContainer);

      if (formContainerHeight > window.innerHeight) {
        formContainerHeight = 10;
        formContainer.style.cssText += 'top: 10px; border: 0px transparent none !important; box-shadow: none !important'
      }
      
      formContainer.style.cssText += 'margin: -'+(formContainerHeight / 2)+'px auto 0px; right: 5.5%; left: auto';
    }
    
    if (mainLogo instanceof HTMLElement) {
      var mainLogoHeight = GetElementHeight (mainLogo);

      if (mainLogoHeight > window.innerHeight) {
        mainLogoHeight = 10;
        mainLogo.style.cssText += 'top: 10px; border: 0px transparent none !important; box-shadow: none !important'
      }
      
      mainLogo.style.cssText += 'margin: -'+(mainLogoHeight / 2)+'px auto 0px;';
    }
  }
];
