import Main from '~/Main';

import { App } from '~/App';
import { Form, SaveButton } from '@components/index';

export function IdentifyTab () {
  const state = App.UseState ({ 
    num: 0,
    name: '',
    fatherName: '',
    motherName: '',
    docCode: '',
    birthday: ''
  });

  function handleInputChange (e) {

    const newState = {};

    if (e.target.name) {
      newState [e.target.name] = e.target.value;
    }

    state.setState (newState);
  }

  function handleSubmit (e) {
    e.preventDefault ();

    console.log (e.target);
  }

  return App.CreateElement (Form, {method: 'post', 'action': '/register', onSubmit: handleSubmit},
    App.CreateElement (Main.TextField, { type: 'text', onChange: handleInputChange, value: state.name, label: 'Nome', name: 'name' }),

    App.CreateElement (Main.ListSelect, { label: 'Tipo de Documento' },
      App.CreateElement ('option', { value: 1 }, 'Select Me'),
      App.CreateElement ('option', { value: 2 }, 'Select Me'),
      App.CreateElement ('option', { value: 3 }, 'Select Me'),
      App.CreateElement ('option', { value: 4 }, 'Select Me'),
      App.CreateElement ('option', { value: 5 }, 'Select Me'),
      App.CreateElement ('option', { value: 6 }, 'Select Me'),
      App.CreateElement ('option', { value: 7 }, 'Select Me'),
      App.CreateElement ('option', { value: 8 }, 'Select Me'),
      App.CreateElement ('option', { value: 9 }, 'Select Me'),
      App.CreateElement ('option', { value: 10 }, 'Select Me')
    ),

    App.CreateElement (Main.TextField, { type: 'text', onChange: handleInputChange, value: state.docCode, label: 'Código do Documento', name: 'docCode' }),
    App.CreateElement (Main.TextField, { type: 'text', onChange: handleInputChange, value: state.birthday, label: 'Data de Nascimento', name: 'birthday' }),

    App.CreateElement (Main.TextField, { type: 'text', onChange: handleInputChange, value: state.fatherName, label: 'Nome do Pai', name: 'fatherName' }),
    App.CreateElement (Main.TextField, { type: 'text', onChange: handleInputChange, value: state.motherName, label: 'Nome da Mãe', name: 'motherName' }),

    App.CreateElement (Main.RadioBoxList, { label: 'Estado Civil', name: 'maritalStatus' },
      App.CreateElement ('option', { value: 1 }, 'Solteiro(a)'),
      App.CreateElement ('option', { value: 2 }, 'Casado(a)'),
      App.CreateElement ('option', { value: 3 }, 'Separado(a) / desquitado(a) / divorciado(a)'),
      App.CreateElement ('option', { value: 4 }, 'Viúvo(a)'),
      App.CreateElement ('option', { value: 5 }, 'Outro')
    ),

    App.CreateElement (SaveButton, {})
  );
};
