import { App } from '~/App';

import { formTab } from '@components/tabs/formTab';
import { keyCodes } from './key-codes';

const autoFocusedInputTypes = 'time date color week'.split (/\s+/);

const tabConfig = {
  store: {
    formTab
  }
};

function getElementChildren (element) {
  return Array.from (element.childNodes).filter (function (elementItem) { 
    return elementItem.nodeType === 1; 
  })
}

function resizeTabElementItems (tabElementItems) {
  tabElementItems.forEach (function (tabElementItem) {
    tabElementItem.style.width = ((100 / tabElementItems.length) + '%');
  });
}

function decorateTabElement (tabElement) {
  const defaultTabElementProps = uolkeo.$$Object ({
    tabIndex: 1,
    currentTab: 0,
    onKeyDown: function (e) {
      const keyCodeToName = {
        "39": "Right",
        "37": "Left",

        "handleRightKey": function () {
          alert ('HEY')
        },

        "handleLeftKey": function () {
          alert ('HEY Left')
        }
      };

      const keyHandlerPropName = [
        'handle', keyCodeToName [e.keyCode] || e.keyCode, 'Key'
      ].join ('');

      if (typeof keyCodeToName [keyHandlerPropName] === 'function') {
        return keyCodeToName [keyHandlerPropName].apply (this, arguments);
      }
    },

    navigateTo: function (tabIndex) {
      if (this.items [tabIndex] instanceof HTMLDivElement) {

        if (tabConfig.store [this.name] 
          && tabConfig.store [this.name][tabIndex]) {
            tabConfig.store [this.name][tabIndex].enabled = true;
        }

        this.items [tabIndex].removeAttribute ('disabled');
        this.items [tabIndex].click ();
      }
    },

    next: function () {
      const nextTabIndex = (!isNaN (this.currentTab) ? this.currentTab : -1) + 1;

      return this.navigateTo.apply (this, [nextTabIndex]);
    },

    prev: function () {
      console.log ('Navigate Prev => 1');
    }
  });

  for (const prop in defaultTabElementProps) {
    const propValue = defaultTabElementProps [prop];

    if (/^on([a-zA-Z])/.test (prop)) {
      uolkeo.$$addEvent (tabElement, prop.replace (/^on/, '').toLowerCase (), function () {
        return typeof propValue === 'function' ? propValue.apply (this, arguments) : null;
      });

      continue;
    }

    tabElement [prop] = propValue;
  }
}

export const TextField = function TextField (props) {
  const ref = {};
  const fieldId = ['field', Math.random ()].join ('');

  function containerRef (sel) {
    if (/\S/.test (ref.input.value) || autoFocusedInputTypes.indexOf (ref.input.type) >= 0) {
      sel.classList.add ('focus');
    }
  }

  const componentTagName = typeof props.long === typeof true && props.long ? 'textarea' : 'input';

  return App.CreateElement ('div', {'class': 'form-field', 'ref': containerRef},
    App.CreateElement ('label', { 'for': fieldId, 'class': 'form-label-container' },
      App.CreateElement ('span', {'class': 'form-label'},
        props.label || 'Untitled'
      )
    ),

    App.CreateElement (componentTagName, MergeObjects (props, {
      'type': typeof props.type === 'string' ? props.type : 'text', 
      'id': fieldId,
      'name': typeof props.name === 'string' ? props.name : 'unnamed', 
      'class': 'form-textfield',
      'ref': function (input) {
        ref.input = input;
        configTextField (input);
      }
    }))
  );
};

export const LongTextField = function LongTextField (props) {
  if (!(typeof props === 'object')) {
    props = {};
  }

  props ['long'] = true;

  return App.CreateElement (TextField, props);
};

export const NumberField = function NumberField (props) {
  if (!(typeof props === 'object')) {
    props = {};
  }

  props ['type'] = 'number';

  return App.CreateElement (TextField, props);
};

export const ListSelect = function ListSelect (props) {

  if (!(props.options instanceof Array)) {
    props.options = [];
  }
  
  props.children
    .filter (childrenOptionsFilter)
    .map (childrenOptionsMap);
  
  const inputRef = {};
  const state = App.UseState ({});

  function childrenOptionsMap (option) {
    props.options.push ({ label: option.label, value: option.value });
  }
  
  function childrenOptionsFilter (option) {
    return Boolean (
      option && 
      option.nodeName && 
      option.nodeType === 1 && 
      option.nodeName === 'OPTION'
    );
  }

  function handleKeyDown () {
    console.log ('UJA');
  }

  function handleListItemClick () {
    console.log ("Selected Value => #" + this.value);

    const selectedElement = this.parentNode.querySelector ('.selected');

    if (selectedElement) {
      selectedElement.classList.remove ('selected');
    }

    this.classList.add ('selected');

    state.setState ({ selectedItem: this.value });
    inputRef.current.value = this.value;
  }

  return App.CreateElement ('div', {
      'class': 'form-field focus',
      'onKeyDown': handleKeyDown, 
      'ref': function (sel) { sel.tabIndex = 1; }
    },

    App.CreateElement ('label', {'class': 'form-label-container'},
      App.CreateElement ('span', {'class': 'form-label'},
        props.label || 'Untitled'
      )
    ),

    App.CreateElement ('input', {type: 'hidden', name: props.name || '', ref: inputRef}),
    
    App.CreateElement ('ul', {'class': 'list'}, 
      props.options && props.options.map (function (listItem) {
        return App.CreateElement ('li', {
            'class': 'list-item' + (listItem.value === state.selectedItem ? ' selected' : ''), 
            'value': listItem.value,
            'ref': function (sel) { sel.value = listItem.value; },
            onClick: handleListItemClick
          },
          listItem.label
        )
      })
    )
  );
};

