export const RadioBoxList = function RadioBoxList (props) {

  if (!(props.options instanceof Array)) {
    props.options = [];
  }
  
  props.children
    .filter (childrenOptionsFilter)
    .map (childrenOptionsMap);

  function childrenOptionsMap (option) {
    props.options.push ({ label: option.label, value: option.value });
  }
  
  function childrenOptionsFilter (option) {
    return Boolean (
      option && 
      option.nodeName && 
      option.nodeType === 1 && 
      option.nodeName === 'OPTION'
    );
  }

  function handleInputChange (e) {
    if (e.target.checked 
      && typeof props.onChange === 'function') {
      const inputContext = { value: e.target.value, name: e.target.name };

      return props.onChange.apply (inputContext, [inputContext]);
    }
  }

  return App.CreateElement ('div', {'class': 'form-radio-field'}, 
    App.CreateElement ('span', {'class': 'form-radio-field-label-text'},
      props.label || 'Untitled'
    ),

    App.CreateElement ('ul', {'class': 'form-radio-field-options-list'},
      props.options.map (function (option) {
        const fieldId = ['field', Math.random ()].join ('');

        return App.CreateElement ('li', {'class': 'form-radio-field-options-list-item'},
          App.CreateElement ('input', {
            'id': fieldId,
            'type': 'radio',
            'value': option.value || '',
            'name': props.name || 'unnamed',
            'onChange': handleInputChange
          }),
          App.CreateElement ('label', {'class': 'form-radio-field-input-container', 'for': fieldId}),
          App.CreateElement ('label', {'class': 'form-radio-field-options-list-item-label', 'for': fieldId}, 
            option.label
          )
        )
      })
    )
  );
};

export const SelectBox = function SelectBox (props) {

  if (!(props.options instanceof Array)) {
    props.options = [];
  }
  
  props.children
    .filter (childrenOptionsFilter)
    .map (childrenOptionsMap);

  const labelRef = {};
  const optionsListRef = {};
  const inputRef = {};
  const selectRef = {};
  const selectedItemRef = {};
  const firstOption = typeof props.options [0] === 'object' && props.options [0] != null ? props.options [0] : {};

  function childrenOptionsMap (option) {
    if (option instanceof HTMLOptionElement) {      
      props.options.push ({
        label: option.label, 
        value: option.value,
        selected
      });
    } else {
      option.map (childrenOptionsMap);
    }
  }
  
  function childrenOptionsFilter (option) {
    return Boolean (
      (
        option && 
        option.nodeName && 
        option.nodeType === 1 && 
        option.nodeName === 'OPTION'
      ) 
      || option instanceof Array 
    );
  }

  function handleLabelClick (e) {
    /*
      console.log ('Label => ', labelRef.current, '\n\n\n\nClick Done => ', e.target);
    */
    if (optionsListRef.current) {
      optionsListRef.current.style.display = optionsListRef.current.style.display === 'none' || !optionsListRef.current.style.display ? 'block' : 'none';
      
      const optionsListItems = Array.from (optionsListRef.current.childNodes).filter (function (optionsListItem) {
        return optionsListItem.nodeType === 1;
      });

      var optionsListItemsTotalHeight = 0;
      
      optionsListItems.map (function (currentItem) {
        optionsListItemsTotalHeight += parseFloat (window.getComputedStyle (currentItem)['height']);
      });

      const selectElementHeight = parseFloat (window.getComputedStyle (selectRef.current)['height']);
      const optionsListMinumumHeight = 220;
      const _optionsListHeight = window.innerHeight - (e.clientY + selectElementHeight);
      const optionsListHeight = _optionsListHeight <= optionsListItemsTotalHeight ? _optionsListHeight : optionsListItemsTotalHeight;

      if (optionsListRef.current.style.display === 'block') {
        if (_optionsListHeight >= optionsListMinumumHeight) {
          optionsListRef.current.style.cssText = 'display: block; height: '+(optionsListHeight + 2)+'px; overflow-y: auto;';          
        } else  {
          const y = e.clientY - 20 <= optionsListItemsTotalHeight ? e.clientY - 20 : optionsListItemsTotalHeight;
          optionsListRef.current.style.cssText = 'display: block; height: '+(y + 5)+'px;top: -'+(y + 5)+'px;box-shadow: rgba(0, 0, 0, 0.34) -2px -6px 9px 1px;overflow-y: auto;';
        }
      }
    }
  }

  function handleSelectKeyDown (e) {
    if (e.keyCode === keyCodes.ESC) {
      labelRef.current.click ();
    }
  }

  function handleSelectBlur () {
    if (optionsListRef.current) {
      optionsListRef.current.style.display = 'none';
    }
  }

  return App.CreateElement ('div', {
      'class': 'custom-select-box select-box', 
      'value': firstOption.value, 
      'ref': function (select) { 
        select.tabIndex = 1; 
        selectRef.current = select;

        const dataSelectLabel = props.selectElement.getAttribute ('data-select-label');

        if (dataSelectLabel) {
          select.setAttribute ('title', dataSelectLabel);
        }

        const originalSelectElementClassList = props.selectElement.className.split (/\s+/);

        for (var i = 0; i < originalSelectElementClassList.length; i++) {
          if (!(['small', 'large'].indexOf (originalSelectElementClassList [i]) < 0)) {
            select.classList.add (originalSelectElementClassList [i]);
            break;
          }
        }

      }, 
      'onBlur': handleSelectBlur, 
      'onKeyDown': handleSelectKeyDown
    },

    App.CreateElement ('label', {'class': 'custom-select-box-label', onClick: handleLabelClick},
      App.CreateElement ('span', {'class': 'custom-select-box-label-content', ref: labelRef},
        firstOption.label || ''
      )
    ),

    App.CreateElement ('input', {
      type: 'hidden', 
      ref: input => {
        inputRef.current = input;

        input.name = props.selectElement.name || '';
      }
    }),

    App.CreateElement ('ul', {'class': 'custom-select-box-options', ref: optionsListRef},
      props.options.map (function (option) {
        function handleOptionClick () {
          inputRef.current.value = option.value;
          labelRef.current.textContent = option.label;
          labelRef.current.click ();

          if (selectedItemRef.current instanceof HTMLLIElement) {
            selectedItemRef.current.classList.remove ('selected');
          }

          this.classList.add ('selected');

          selectedItemRef.current = this;
        }

        return App.CreateElement ('li', {
            'class': 'custom-select-box-option', 
            value: option.value, 
            onClick: handleOptionClick,

            ref: itemRef => {
              if (option.selected) {
                inputRef.current.value = option.value;
                labelRef.current.textContent = option.label;

                if (selectedItemRef.current instanceof HTMLLIElement) {
                  selectedItemRef.current.classList.remove ('selected');
                }

                itemRef.classList.add ('selected');

                selectedItemRef.current = itemRef;
              }
            }
          }, 
          option.label
        );
      })
    )
  );
};

export const TabListItem = function TabListItem (props) {
  return App.CreateElement ('div', {'class': 'tab-list-item-container'}, 
    App.CreateElement ('div', {
        'class': 'tab-list-item', 
        'title': props.title || 'Untitled'
      },

      App.CreateElement ('i', {
          'class': 'fa',
          'ref': function (sel) {
            if (typeof props.icon === typeof 'str') {
              sel.classList.add ('fa-' + props.icon.replace (/^fa-/i, ''));
            }
          }
        }
      ),
      App.CreateElement ('i', {'class': 'tab-list-item-text'}, 
        props.title || 'Untitled'
      )
    )
  );
};

export const configTextField = function configTextField (textField) {
  const classList = [
    'focus',
    'highlight'
  ];

  if (/\S/.test (textField.value) && textField.parentNode) {
    textField.parentNode.classList.add ('focus');
  }

  uolkeo.$$addEvent (textField, 'focus', function () {
    const parent = this.parentNode;

    classList.forEach (function (className) {
      parent.classList.add (className);
    });
  });

  uolkeo.$$addEvent (textField, 'blur', function () {
    const parent = this.parentNode;

    parent.classList.remove ('highlight');

    if (!/\S/.test (this.value) && autoFocusedInputTypes.indexOf (this.type) < 0) {
      classList.forEach (function (className) {
        parent.classList.remove (className);
      });
    }
  });
};

export const configListSelect = function configListSelect (listElement) {
  const listElementOptions = Array.from (listElement.childNodes).filter (function (option) {
    return option.nodeType === 1;
  })
  .map (function (option) {
    return { label: option.textContent.trim (), value: option.value };
  });

  const label = listElement.getAttribute ('data-list-select');

  const customListElement = App.CreateElement (ListSelect, { options: listElementOptions, label: label, name: listElement.name });

  listElement.parentNode.insertBefore (customListElement, listElement);
  listElement.parentNode.removeChild (listElement);
};

export const configAutoSelectField = function configAutoSelectField (selectElement) {
  const selectElementOptions = Array.from (selectElement.childNodes)
          .filter (option => option.nodeType === 1);

  const options = selectElementOptions.map (option => {
    const selectElementValue = selectElement.value;
    return { 
      value: option.value, 
      label: option.textContent.trim (),
      selected: selectElementValue == option.value 
    };
  });

  selectElement.parentNode.insertBefore (
    App.CreateElement (SelectBox, { options, selectElement }),
    selectElement
  );

  selectElement.parentNode.removeChild (selectElement);
};

export const configTabControll = function configTabControll (tabElement) {

  const tabName = tabElement.getAttribute ('data-tab');
  const tabBodyElement = document.querySelector ('[data-tab-body][data-tab-name='+ tabName +']');
  const tabTitleElement = document.querySelector ('[data-tab-title][data-tab-name='+ tabName +']');
  const tab = tabConfig.store [tabName];

  decorateTabElement (tabElement);

  if (!uolkeo.isArray (tab)) {
    console.error ("Attribute 'data-tab' is not a valid tab reference.", tabElement);
  }

  tabElement.innerHTML = '';
  tab.forEach (function (tabItem) {
    tabElement.appendChild (
      App.CreateElement (TabListItem, tabItem)
    );
  });

  const tabElementItems = getElementChildren (tabElement);

  /**console.log ('TAB INDEX => ', tabElement.currentTab);*/
  
  /** tabBodyElement.innerHTML = 'Loading..!'; */
  
  tabElement.items = tabElementItems;
  tabElementItems.forEach (function (tabElementItem, tabElementItemIndex) {
    resizeTabElementItems (tabElementItems);

    const tabItem = tab [tabElementItemIndex];

    if (typeof tabItem === 'object') {
      tabItem.index = tabElementItemIndex;
      /**
       * disable the tab item if it have to
       */
      if (!(tabItem.enabled !== false)) {
        tabElementItem.setAttribute ('disabled', 'disabled');
      }

      /**
       * add the click event to the tab list item element
       * in order rendering the component that points to the
       * item reference.
       */
      uolkeo.$$addEvent (tabElementItem, 'click', function () {
        if (tabItem.enabled === false) {
          return;
        }

        tabItem.enabled = true;

        if (tabTitleElement) {
          tabTitleElement.innerHTML = typeof tabItem.title === typeof 'str' ? tabItem.title : 'Untitled'; 
        }

        tabBodyElement.innerHTML = '';
        tabBodyElement.appendChild (App.CreateElement (tabItem.component, null));

        const selectedElement = this.parentNode.querySelector ('.tab-list-item-container.selected');

        if (selectedElement) {
          selectedElement.classList.remove ('selected');
        }

        this.classList.add ('selected');
        this.parentNode.currentTab = tabItem.index;

        tabBodyElement.parentNode.style.overflow = 'hidden';
        tabBodyElement.classList.add ('tab-body-element-anim');

        setTimeout (function () {
          tabBodyElement.classList.remove ('tab-body-element-anim');
          tabBodyElement.parentNode.style.overflow = 'inherit';
        }, 505);
      });

    } else {
      tabElementItem.parentNode.removeChild (tabElementItem);
      resizeTabElementItems (getElementChildren (tabElement));
    }
  });


  tabElement.navigateTo (tabElement.currentTab);
};

export const configDropDown = function configDropDown (dropDownElement) {
  if (!(dropDownElement instanceof HTMLElement)) {
    return;
  }

  function getDropDownMenu (dropDown) {
    var dropDownElementMenu = dropDown.querySelector ('[data-dropdown-menu]');

    if (!dropDownElementMenu) {
      const dropDownName = dropDown.getAttribute ('data-dropdown');
      
      dropDownElementMenu = document.querySelector (
        '[data-dropdown-menu='+dropDownName+']'
      );
    }

    return dropDownElementMenu;
  }

  function closeDropDown (dropDown, options = {}) {
    const dropDownElementMenu = getDropDownMenu (dropDown);

    if (dropDownElementMenu) {
      if (options.delay !== false) {
        setTimeout (function () { return perform (dropDownElementMenu); }, 200);
      } else {
        return perform (dropDownElementMenu)
      }
    }

    function perform (dropDownElementMenu) {
      dropDownElementMenu.style.cssText = 'display: none;';

      const dropDownElementMenuBody = dropDownElementMenu
        .querySelector ('.data-dropdown-menu-body');
      
      if (dropDownElementMenuBody) {
        dropDownElementMenuBody.style.cssText = '';
      }
    }
  }

  dropDownElement.tabIndex = 1;

  const dropDownElementMenuEl = getDropDownMenu (dropDownElement);

  const dropDownElementMenuLinks = (
    Array.from (dropDownElementMenuEl.childNodes)
    .filter (function (childNode) { 
      return childNode.nodeType == 1 && ['H1', 'H2'].indexOf (childNode.nodeName) < 0; 
    })
  );

  const dropDownElementMenuBody = App.CreateElement ('div', {'class': 'data-dropdown-menu-body'}, dropDownElementMenuLinks);

  dropDownElementMenuEl.appendChild (dropDownElementMenuBody);

  uolkeo.$$addEvent (dropDownElement, 'blur', function () {
    closeDropDown (this);
  });

  uolkeo.$$addEvent (dropDownElement, 'keydown', function (e) {
    if (e.keyCode == 27) { closeDropDown (this, { delay: false }); }
  });

  uolkeo.$$addEvent (dropDownElement, 'click', function (e) {
    if (e.preventDefault) { 
      e.preventDefault (); 
    }

    const dropDownElementMenu = getDropDownMenu (this);

    if (dropDownElementMenu) {

      /** this.parentNode.insertBefore (dropDownElementMenu, this);
      */
      
      document.body.appendChild (dropDownElementMenu);

      const dropDownElementMenuDisplay = GetElementStyleProp (dropDownElementMenu, 'display');
      
      /* dropDownElementMenu.style.top = '0px'; */
      dropDownElementMenu.style.display = dropDownElementMenuDisplay == 'none' ? 'block' : 'none';

      if (GetElementStyleProp (dropDownElementMenu, 'display') != 'none') {
        
        const dropDownElementMenuHeight = GetElementHeight (dropDownElementMenu);
        const dropDownElementMenuWidth = GetElementWidth (dropDownElementMenu);
        
        var dropDownElementMenuX = e.clientX;
        var dropDownElementMenuY = e.clientY;

        if ((e.clientY + dropDownElementMenuHeight) > window.innerHeight - 10) {
          dropDownElementMenuY -= (dropDownElementMenuHeight);
        }

        var css = 'top: '+dropDownElementMenuY+'px !important; display: block;';
        /*
        console.log (this, window.getComputedStyle (this)['height']);

        console.log ('top: '+e.pageY+'px !important; left: '+(dropDownElementMenuX)+'px !important; display: block;');
        */
        const dropDownWidth = this.getAttribute ('data-dropdown-width');

        if (dropDownWidth) {
          css += 'width: '+dropDownWidth+'px;';
        }

        dropDownElementMenu.style.cssText = css;

        if ((e.clientX + GetElementWidth (dropDownElementMenu)) > window.innerWidth - 10) {
          dropDownElementMenuX -= (GetElementWidth (dropDownElementMenu) * 1);
        }

        if (dropDownElementMenuX < 0) {
          dropDownElementMenu.style.cssText += 'left: 0px; right: 0px; margin-left: auto; margin-right: auto; width: 97%;'; 
        } else {
          dropDownElementMenu.style.cssText += ('left: ' + dropDownElementMenuX + 'px !important;');
        }
        
        if (dropDownElementMenuY < 0 || (e.clientY + GetElementHeight (dropDownElementMenu)) > window.innerHeight - 10) {
          dropDownElementMenu.style.cssText += 'top: '+(e.clientY)+'px !important; display:  block; height: '+(window.innerHeight - (e.clientY + 20))+'px; overflow-y: auto;';
        }

        /*
          dropDownElementMenu.style.top =  + 'px';
          dropDownElementMenu.style.left =  + 'px';
        */
       
        const dropDownElementMenuBodyElement = dropDownElementMenu.querySelector ('.data-dropdown-menu-body');
        const dropDownElementMenuTitleElement = (
          dropDownElementMenu.querySelector ('h1') ||
          dropDownElementMenu.querySelector ('h2')
        );

        var dropDownElementMenuCurrentHeight = GetElementHeight (dropDownElementMenu);
        /*
          console.log (dropDownElementMenuTitleElement, dropDownElementMenuCurrentHeight, GetElementHeight (dropDownElementMenuTitleElement));
          

          console.log ('El => ', dropDownElementMenuBodyElement);
        */
        if (dropDownElementMenuTitleElement) {
          dropDownElementMenuCurrentHeight -= (GetElementHeight (dropDownElementMenuTitleElement) + 7);
        } else if (dropDownElementMenuBodyElement) {
          dropDownElementMenuBodyElement.style.margin = '0px';
        }

        if (dropDownElementMenuBodyElement) {
          dropDownElementMenuBodyElement.style.height = (dropDownElementMenuCurrentHeight - 0) + 'px';
        }
      } else {
        closeDropDown (this);
      }
    }
  });
};

export const MergeObjects = function MergeObjects () {
  const object = {};

  for (var i = 0; i < arguments.length; i++) {
    const argument = arguments [i];
    /**
     * Make sure the current argument is an object
     */
    if (typeof argument === 'object') {
      /**
       * Map whole the props and define each one
       * in the final object
       */
      for (var key in argument) {
        object [key] = argument [key];
      }
    }
  }

  return object;
};

export const GetElementStyleProp = function GetElementStyleProp (element, property) {
  if (!(element instanceof HTMLElement)) {
    return null;
  }
  
  const styles = window.getComputedStyle (element);

  if (typeof property === typeof 'str') {
    return styles [property];
  }

  return styles;
};

export const GetElementHeight = function GetElementHeight (element) {
  return parseFloat (GetElementStyleProp (element, 'height'));
};

export const GetElementWidth = function GetElementWidth (element) {
  return parseFloat (GetElementStyleProp (element, 'width'));
}

export const Tab = uolkeo.$$Object ({
  get: function (tabName) {
    if (!(tabConfig.store [tabName] instanceof Array)) {
      return null;
    }

    const tabElement = document.querySelector ('[data-tab="'+tabName+'"]');

    if (tabElement) {
      tabElement.name = tabName;
      return { element: tabElement, items: tabConfig.store [tabName] };
    }
  },

  find: function (tabName) {
    if (!(tabConfig.store [tabName] instanceof Array)) {
      return null;
    }

    const tabElement = document.querySelector ('[data-tab="'+tabName+'"]');

    if (tabElement) {
      tabElement.name = tabName;
      return tabElement;
    }
  },

  getElement: function () {
    return Tab.find.apply (this, arguments);
  },

  getItems: function (tabName) {
    if (!!(tabConfig.store [tabName] instanceof Array)) {
      return tabConfig.store [tabName];
    }
  }
});

