import Main, { GetElementWidth } from "./Main";

function resizePageElements () {
  const asideMenuList = uolkeo.$$qs ('.page-main-aside-menu-list');
  const asideFooter = uolkeo.$$qs ('.page-main-aside-footer');
  const pageTopContainer = uolkeo.$$qs ('.page-top-content-container');

  const pageMainContentNav = uolkeo.$$qs ('.page-main-content-nav');
  const pageMainContentBody = uolkeo.$$qs ('.page-main-content-body');
  const pageMainContentBodyContainer = uolkeo.$$qs ('.page-main-content-body-container');
  const pageMainContentBodyFooter = uolkeo.$$qs ('.page-main-content-body-footer');

  const pageMainContent = uolkeo.$$qs ('.page-main-content');

  function getElementHeight (element) {
    return parseFloat (window.getComputedStyle (element)['height']);
  }

  function getElementChildren (element) {
    return Array.from (element.childNodes).filter (el => el.nodeType === 1); 
  }

  if (!!(asideMenuList && pageTopContainer)) {
    var pageTopContainerHeight = getElementHeight (pageTopContainer);
    const asideFooterHeight = getElementHeight (asideFooter);

    if (asideFooter && Main.GetElementStyleProp(asideFooter, 'display') !== 'none') {
      pageTopContainerHeight += asideFooterHeight;
    }

    const newAsideMenuListHeight = parseFloat (window.innerHeight - (pageTopContainerHeight));
    const newMainContentContainerHeight = parseFloat (window.innerHeight - (getElementHeight (pageTopContainer)));

    asideMenuList.style.height = newAsideMenuListHeight + 'px';
    
    if (pageMainContent) {
      pageMainContent.style.cssText = 'height: '+ newMainContentContainerHeight +'px;';
    }
  }

  if (!!(pageMainContentBodyFooter && pageMainContentBody)) {
    var pageMainContentBodyPaddingBottomValue = getElementHeight (pageMainContentBodyFooter);

    const pageMainContentBodyHeight = getElementHeight (pageMainContentBody);
    const pageMainContentBodyPaddingBottom = 0 /* arseFloat (window.getComputedStyle (pageMainContentBody)['paddingBottom']) */;
    
    if (!isNaN (pageMainContentBodyPaddingBottom)) {
      pageMainContentBodyPaddingBottomValue += pageMainContentBodyPaddingBottom;
    }

    if (pageMainContentBodyContainer && uolkeo.$$qs ('.dash-home-content-container')) {
      pageMainContentBodyContainer.style.height = (getElementHeight (pageMainContentBody) - getElementHeight (pageMainContentBodyFooter) - pageMainContentBodyPaddingBottomValue) + 'px';
    }

    pageMainContentBody.style.paddingBottom = pageMainContentBodyPaddingBottomValue + 'px';

    if (pageTopContainer) {
      const windowHeightBeyondMain = window.innerHeight - getElementHeight (pageTopContainer);

      if (windowHeightBeyondMain > pageMainContentBodyHeight) {
        var pageMainContentBodyNewHeight = pageMainContentBodyHeight + (windowHeightBeyondMain - pageMainContentBodyHeight);

        if (pageMainContentNav) {
          pageMainContentBodyNewHeight -= getElementHeight (pageMainContentNav);
        }
      
        pageMainContentBody.style.minHeight = pageMainContentBodyNewHeight + 'px';
      }
    }
  }

  document.querySelectorAll ('.data-form-tab-controll-item').forEach (item => {
    const newItemHeight = getElementHeight (item.parentNode) / getElementChildren (item.parentNode).length;

    item.style.height = newItemHeight + 'px';

  });

}

export function DashboardPageMainAction () {
  function loop () {
    resizePageElements ();

    const pageTopHeaderUserBoxElement = uolkeo.$$qs ('.page-top-header-user-box');
    const pageTopHeaderUserBoxIconsElement = uolkeo.$$qs ('.page-top-header-user-box-icons');
    
    if (!(pageTopHeaderUserBoxElement && pageTopHeaderUserBoxIconsElement)) {
      return;
    }

    const pageTopHeaderUserBoxIconsList = Array.from (pageTopHeaderUserBoxIconsElement.childNodes)
      .filter (el => el.nodeType === 1)
      .map (el => el);

    const pageTopHeaderUserBoxElementsList = Array.from (pageTopHeaderUserBoxElement.childNodes)
      .filter (el => el.nodeType === 1)
      .map (el => el);
    
    const pageTopHeaderUserBoxElementsListItemsWidth = pageTopHeaderUserBoxElementsList.map (el => GetElementWidth (el));
    const pageTopHeaderUserBoxElementsListItemsWidthSum = pageTopHeaderUserBoxElementsListItemsWidth.reduce ((width, nextWidth) => width + nextWidth);

    const pageTopHeaderUserBoxIconsListItemsWidth = pageTopHeaderUserBoxIconsList.map (el => GetElementWidth (el));
    const pageTopHeaderUserBoxIconsListItemsWidthSum = pageTopHeaderUserBoxIconsListItemsWidth.reduce ((width, nextWidth) => width + nextWidth);
    const pageTopHeaderUserBoxIconsListNewWidth = pageTopHeaderUserBoxIconsListItemsWidthSum + (8 * pageTopHeaderUserBoxIconsList.length) + 30;

    if (pageTopHeaderUserBoxIconsElement) {
      pageTopHeaderUserBoxIconsElement.style.width = pageTopHeaderUserBoxIconsListNewWidth + 'px';
    }

    if (pageTopHeaderUserBoxElement) {
      pageTopHeaderUserBoxElement.style.width = (pageTopHeaderUserBoxElementsListItemsWidthSum + 20) + 'px';
    }
      
    window.requestAnimationFrame (loop);
  }

  loop ();

  uolkeo.$$addEvent ('.page-top-header-user-box-icon-search-link', 'click', e => {
    e.preventDefault ();

    const searchPopup = uolkeo.$$qs ('.top-header-search-popup');

    if (searchPopup) {
      searchPopup.classList.toggle ('hide');
    }
  });

  uolkeo.$$addEvent ('.page-top-header-user-box-icon-search-link', 'blur', e => {
    e.preventDefault ();

    const searchPopup = uolkeo.$$qs ('.top-header-search-popup');

    if (searchPopup) {
      searchPopup.classList.add ('hide');
    }
  });

  uolkeo.$$addEvent (window, 'resize', resizePageElements);
}
