import Main from '~/Main';

import { App } from '~/App';
import { Form, SaveButton } from '@components';

export function SchoolarityTab () {
  return App.CreateElement (Form, {method: 'post', 'action': '/register', onSubmit: () => null}, 
    App.CreateElement ('div', null,
      App.CreateElement ('div', {'class': 'form-auto-select-field'},
        App.CreateElement ('label', {'class': 'form-label-container'}, 
          App.CreateElement ('span', {'class': 'form-label'}, 'Nível de Escolaridade')
        ),  
        App.CreateElement (Main.SelectBox, {value: 1},
          App.CreateElement ('option', { value: 1 }, 'Analfabeto'),
          App.CreateElement ('option', { value: 2 }, '1º Grau'),
          App.CreateElement ('option', { value: 3 }, '2º Grau'),
          App.CreateElement ('option', { value: 3 }, 'Superior')
        )
      ),

      App.CreateElement (Main.RadioBoxList, { label: 'Estado' },
        App.CreateElement ('option', { value: 1 }, 'Completo'),
        App.CreateElement ('option', { value: 2 }, 'Incompleto'),
        App.CreateElement ('option', { value: 3 }, 'Cursando')
      ),

      App.CreateElement (SaveButton, {})
    ) 
  );
};
